<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>设置首页楼层展示数据</li>
    </cms-prompt>
      <section class="cms-batch" style="border-top:0;">
        <cms-button icon="add" type="success" size="small" @click="$routerLink('/floorMange/save','save')">新增</cms-button>
      </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" ref='dataTable'>
      <el-table-column prop="id" label="id" width="80"></el-table-column>
      <el-table-column prop="floorName" label="楼层名称">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.floorName">{{scope.row.floorName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="discountAmount" label="是否显示">
        <template slot-scope="scope">
          <cms-switch v-model="scope.row.isShow" :id="scope.row.id" @change="toggleVisable"></cms-switch>
        </template>
      </el-table-column>
      <el-table-column prop="remark" label="说明">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作选项" width='280' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click="$routerLink('/floorMange/update','update',{id:scope.row.id})">编辑</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import {
  indexlayoutPage,
  indexlayoutShow,
  indexlayoutHide
} from '@/api/marketing/index.js'
import list from '@/mixins/list'
var defaultData = {
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    toggleVisable(status, id) {
      if (status) {
        indexlayoutShow({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      } else {
        indexlayoutHide({ ids: [id] }).then(res => {
          if (res.code !== 200) {
            this.$errorMessage('操作失败')
          }
        })
      }
    }
  },
  created() {
    this.getTableData(indexlayoutPage, this.queryParams)
  }
}
</script>
<style>
</style>



